<template>
  <div class="addBlog">
    <div class="addMain">
      <div class="row">
        <div class="text">博客标题:</div>
        <div class="inpBox">
          <input type="text" v-model="itemObj.title" />
        </div>
      </div>

      <div class="row">
        <div class="text">作者:</div>
        <div class="inpBox">
          <input type="text" v-model="itemObj.author" />
        </div>
      </div>

      <div class="row">
        <div class="text">分类:</div>
        <div class="inpBox">
          <select v-model="itemObj.classify">
            <option v-for="item in classList" :key="item.id">
              {{ item.classname }}
            </option>
          </select>
        </div>
      </div>

      <div class="row">
        <div class="text">博客内容:</div>
        <div class="inpBox">
          <textarea v-model="itemObj.content"></textarea>
        </div>
      </div>

      <div class="row">
        <div class="inpBox">
          <button @click="onSubmit">发表博客</button>
        </div>
      </div>
    </div>

    <div v-show="itemObj.title||itemObj.content||itemObj.author">
      <h1 class="blShowBox">博客预览</h1>
      <BlogItem :item="itemObj"></BlogItem>
    </div>
  </div>
</template>

<script>
// import axios from "axios";
import BlogItem from './BlogItem.vue'
export default {
  data () {
    return {
      itemObj: {
        title: '',
        posttime: new Date().getTime() / 1000,
        classify: 'HTML5+CSS3',
        author: '',
        content: ''
      },
      classList: []
    }
  },
  components: {
    BlogItem
  },
  created () {
    this.getClassify()
  },
  methods: {
    // 提交数据
    onSubmit () {
      if (this.itemObj.title == '' ||
      this.itemObj.content == '' ||
      this.itemObj.classify == '' ||
      this.itemObj.author == '') {
        alert('请填写完整你的Blog！')
        return
      }
      this.pushData()
      alert('发布成功返回首页')
      this.$router.push('/')
    },
    pushData () {
      this.axios({
        url: 'https://ku.qingnian8.com/dataApi/blog/addBlog.php',
        params: this.itemObj
      }).then(res => {
        console.log(res)
      })
    },
    // 获取分类列表
    getClassify () {
      this.axios({
        url: 'https://ku.qingnian8.com/dataApi/blog/classBlog.php'
      }).then((res) => {
        this.classList = res.data
      })
    }
  }
}
</script>

<style lang="less" scoped>
input,
textarea,
select {
  outline: none;
}
.addBlog {
  padding: 50px 30px;
  min-height: 830px;
}
.addMain {
  padding: 0 80px;
  font-size: 18px;
  color: #333;
  .row {
    padding: 10px 0;
    display: flex;
    align-items: center;
    .text {
      width: 15%;
      text-align: right;
      padding-right: 10px;
    }
    .inpBox {
      width: 80%;
      input,
      textarea {
        width: 100%;
        padding: 10px;
        height: 38px;
        border: 1px solid #ccc;
        border-radius: 3px;
        transition: 0.3s;
        font-size: 16px;
        color: #666;
        &:focus {
          border-color: #41b883;
          box-shadow: 0 0 10px rgba(65, 184, 131, 0.3);
        }
      }
      select {
        width: 50%;
        height: 38px;
        border: 1px solid #ccc;
        border-radius: 3px;
        padding: 0 10px;
        font-size: 16px;
        color: #333;
      }
      textarea {
        height: 150px;
      }
      button {
        width: 50%;
        height: 30px;
        margin-left: 40%;
        background-color: #41b883;
        color: #fff;
        border: none;
        border-radius: 3px;
        cursor: pointer;
        transition: 0.3s;
        &:hover {
          background: #2f6f52;
        }
      }
    }
  }
  .rowArea {
    align-items: flex-start;
  }
}

.blShowBox {
  margin-top: 30px;
  padding: 50px 0 30px;
  font-size: 22px;
  color: #555;
  border-top: 1px solid #e4e4e4;
  text-align: center;
}
</style>
